
  .About-header {
    background-color: #369091;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: "Lato", sans-serif;
    font-size: calc(6px + 2vmin);
    color: #78CDD7;
  }
  
  .About-header h1 {
    font-size: calc(100px + 2vmin);
    font-family: "Amatic SC";
    color: #FFFFFA;
  }

  .page-title {
      flex-direction: row;
      align-items:flex-start;
  }

  .img {
      height: 20%;
      width: 25%;
      border-radius:100%;
  }